import { Component, Prop, h, Element } from '@stencil/core';
import { utils } from '../../utils/utils';

@Component({
  tag: 'yeti-page-contents',
  shadow: false,
})
export class YetiPageContents {

  @Element() el: HTMLElement;

  /**
   * Whether to show the Page Contents menu in expanded or closed state.
   */
  @Prop() isExpanded: boolean = false;


  /**
   * Whether to show the Page Contents menu in expanded or closed state.
   */
  @Prop() wrapperId: string = ""; // Will be set in componentWillLoad;


  componentWillLoad() {
    
    let elId = this.el.id;

    if (!elId) {
      elId = utils.generateUniqueId();
      this.wrapperId = `${elId}_wrapper`;
    }

  }



  render() {

    return (
      
      <div class="yeti-page_contents-wrapper" id={this.wrapperId}>Page Contents Under Construction</div>

    );
  }
  

}
